html,
body {
  background-color: transparent;
  background-color: transparent;
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: "Oswald", sans-serif;
  font-size: 10px;
  color: #333333;
}

@media (min-width: 641px) {
  body {
    font-size: 14px;
  }
}

ol,
ul,
li,
p,
a,
span {
  font-size: 10px;
}

@media (min-width: 641px) {
  ol,
  ul,
  li,
  p,
  a,
  span {
    font-size: 14px;
  }
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: #333333;
  cursor: pointer;
  outline: none;
  text-decoration: none;
}

a:active, a:hover {
  color: #4d4d4d;
}

img {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before, .comments-area .comment-respond .respond-inputs:before, .container-single-post-nav .nav-links:before,
.clearfix:after,
.comments-area .comment-respond .respond-inputs:after,
.container-single-post-nav .nav-links:after {
  content: " ";
  display: table;
}

.clearfix:after, .comments-area .comment-respond .respond-inputs:after, .container-single-post-nav .nav-links:after {
  clear: both;
}

.button {
  display: inline-block;
  margin: 10px 0;
  padding: 5px 10px;
  background-color: #cfbede;
  border: 1px solid #ffffff;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}

@media (min-width: 1024px) {
  .button {
    padding: 10px 20px;
  }
}

@media (min-width: 1024px) {
  .button {
    background-color: transparent;
  }
}

.button:hover, .button:active, .button:focus {
  background-color: #bba3d0;
}

.read-more {
  position: relative;
  display: inline-block;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.read-more:after {
  content: "";
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  margin-left: 5px;
  border-style: solid;
  border-width: 8px 0 8px 10px;
  border-color: transparent transparent transparent #cfbede;
  opacity: 0;
  transform-style: preserve-3d;
  transform: translate3d(-15px, -50%, 0px);
  transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.read-more:hover:after {
  transform: translate3d(0px, -50%, 0px);
  opacity: 1;
}

.divider {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 50%;
  height: 2px;
  background-color: #cfbede;
  margin: 0 auto;
}

@media (min-width: 641px) {
  .divider {
    display: none;
  }
}

header {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #e3daec;
}

@media (min-width: 641px) {
  header {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

header .header-top form {
  float: right;
  position: relative;
  display: inline-block;
  width: 81%;
  height: 22px;
}

header .header-top form.active input {
  width: 100%;
  padding: 1px 25px 1px 5px;
}

header .header-top input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  width: 0;
  height: 22px;
  padding: 0;
  transition: width 0.4s ease-in-out, padding 0.4s ease-in-out;
}

header .header-top input:focus {
  width: 100%;
  padding-right: 50px;
}

header .header-top span {
  position: absolute;
  top: 0;
  right: 0;
  width: 22px;
  height: 22px;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #bba3d0;
  cursor: pointer;
}

header .header-top span.active, header .header-top span:hover {
  background-color: green;
}

header .header-top a:not([href="/"]) {
  float: right;
  margin-left: 2%;
  text-transform: uppercase;
}

header .header-bottom {
  padding-top: 30px;
}

header .header-bottom ul.header-continents {
  list-style-type: none;
  margin: 0;
}

header .header-bottom li {
  position: relative;
  float: left;
  width: 14.28%;
  text-align: center;
  overflow: hidden;
  height: 64px;
}

header .header-bottom li.active a span:first-child {
  transform: translate3d(0, 0, 0);
}

@media (min-width: 440px) and (max-width: 641px) {
  header .header-bottom li {
    height: 84px;
  }
}

@media (min-width: 641px) {
  header .header-bottom li {
    height: 100px;
  }
}

@media (min-width: 1024px) {
  header .header-bottom li:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 2px solid #bba3d0;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
  }
  header .header-bottom li.active:before, header .header-bottom li:hover:before {
    opacity: 1;
  }
}

header .header-bottom a {
  display: block;
  text-transform: uppercase;
  padding: 5px 0 10px;
}

header .header-bottom a img {
  margin-top: 20px;
}

@media (min-width: 641px) {
  header .header-bottom a img {
    margin-top: 0;
  }
}

header .header-bottom a img.icon_africa {
  width: 45%;
  max-width: 36px;
}

header .header-bottom a img.icon_antarctica {
  width: 50%;
  max-width: 43px;
}

header .header-bottom a img.icon_asia {
  width: 60%;
  max-width: 50px;
}

header .header-bottom a img.icon_australia {
  width: 55%;
  max-width: 54px;
}

header .header-bottom a img.icon_europe {
  width: 65%;
  max-width: 60px;
}

header .header-bottom a img.icon_north_america {
  width: 80%;
  max-width: 74px;
}

header .header-bottom a img.icon_south_america {
  width: 35%;
  max-width: 30px;
}

header .header-bottom a span:first-child {
  position: absolute;
  display: block;
  width: 100%;
  font-size: 8px;
  transform: translate3d(100%, 0, 0);
  transition: transform 0.4s ease-in-out;
}

@media (min-width: 641px) {
  header .header-bottom a span:first-child {
    position: relative;
    width: auto;
    margin-bottom: 10px;
    font-size: initial;
    transform: translate3d(0, 0, 0);
  }
}

header .header-bottom a span:first-child.text_long {
  width: 116%;
}

header .header-bottom a span:first-child.active {
  transform: translate3d(0, 0, 0);
}

header .header-bottom a span:last-child {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  font-size: 8px;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.4s ease-in-out;
}

@media (min-width: 641px) {
  header .header-bottom a span:last-child {
    display: none;
  }
}

header .header-bottom a span:last-child.active {
  transform: translate3d(0, 0, 0);
}

